import React, { Component } from "react";
import { connect } from "react-redux";
import { reduxForm, Field } from "redux-form";
import validasiMutasiCiok from "../../Validations/validasiMutasiCiok";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormHeadMutasiCiok extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-md-3 form-group">
              <Field
                name="noMutasi"
                label="Nomor Mutasi"
                component={renderField}
                type="text"
                onLoad={this.props.change("noMutasi", this.props.noFaktur)}
                readOnly={true}
              />
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="tanggal"
                label="Tanggal"
                component={renderField}
                type="date"
              />
            </div>
            <div className="col-md-3 form-group">
              <label>Transaksi</label>
              <Field
                component="select"
                name="transaksi"
                className="form-control"
              >
                <option value="DEFAULT" key="DEFAULT">
                  SILAHKAN PILIH
                </option>
                <option value="AMBIL">AMBIL</option>
                <option value="BUANG">BUANG</option>
              </Field>
            </div>
            <div className="col-lg-3 col-md-3">
              <div className="form-group">
                <label htmlFor="">.</label>
                <div className="form-control-transparant">
                  <div className="row">
                    <div className="col-lg-6">
                      <label className="mr-3">
                        <Field
                          name="jenisMutasi"
                          component="input"
                          type="radio"
                          value="mutasiCiok"
                          className="mr-2"
                        />
                        Mutasi Ciok
                      </label>
                    </div>
                    <div className="col-lg-6">
                      <label>
                        <Field
                          name="jenisMutasi"
                          component="input"
                          type="radio"
                          value="kirimBahanProduksi"
                          className="mr-2"
                        />
                        Kirim Bahan Produksi
                      </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-3 form-group">
              <Field
                name="noBon"
                label="Nomor Bon"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-md-6 form-group">
              <Field
                name="keterangan"
                label="Keterangan"
                component={renderField}
                type="text"
              />
            </div>
            <div className="col-lg-12 col-md-12 mb-3">
              <div className="row">
                <div className="col-lg-6">
                  <div className="text-left">
                    <button className="btn btn-primary" type="submit">
                      Simpan
                    </button>
                  </div>
                </div>
                <div className="col-lg-6">
                  <div className="text-right">
                    <button
                      className="btn btn-primary"
                      data-toggle="modal"
                      data-target="#primary-header-modal"
                      type="button"
                    >
                      Tambah Barang
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormHeadMutasiCiok = reduxForm({
  form: "FormHeadMutasiCiok",
  enableReinitialize: true,
  validate: validasiMutasiCiok,
})(FormHeadMutasiCiok);
export default connect()(FormHeadMutasiCiok);
